.App {
    margin: 0rem;
    font-family: Arial, Helvetica, sans-serif;
    

  }
  .background{
    background-image: url("https://i.ibb.co/t2JJfCp/imageedit-5-2216238736.png");
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: 300px 300px; /* Resize the background image to cover the entire container */
  }
  .heading{
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(55, 17, 194);
    padding-top: 10px;
    padding-bottom: 10px;
    image-resolution: 100px;
    
    background-color: lightblue;
    backface-visibility: visible;
    text-transform: uppercase;
  }

  .about{
    color: rgb(42, 31, 194);
    text-align: center;
    letter-spacing: initial;
    padding-top: 20px;
    padding-bottom: 20px;
    
  
  }
  .footing{
    color: rgb(55, 17, 194);
    text-align: center;
    letter-spacing: inherit;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: ightblue;
  }

  .ul{
    list-style-type: none;
  }

.item1 { grid-area: header; }
/* .item2 { grid-area: menu; } */
.item3 { grid-area: main; }
/* .item4 { grid-area: right; } */
.item5 { grid-area: footer; }

.grid-container {
 display: grid;
  grid-template-areas:
    'header header header header header header'
    'main main main main main main'
    'footer footer footer footer footer footer';
  grid-gap: 0px;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  
  padding: 1px;
}

.grid-container > div {
  background-color: lightblue;
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
}

.menu{
  width: 100%;
  list-style: none;
  text-align: right;

}

  .menu li a {
    text-align: center;
    font-weight: 300;
    align-items: center;
    display: flex;
    color: rgb(55, 17, 194)f;
    text-decoration: none;
  }
 
  .menu li a:before {
    text-align: center;
    content: "";
    height: 1px;
    width: 0;
    background: rgb(55, 17, 194);
    margin-right: 5px;
    margin-left: 5px;
    transition: 0.3s;
  }

  .menu li a:hover::before{
    text-align: center;
    width: 15px;
  }

  .shine{
    text-align: center;
    font-size: 50px;
    font-weight: 700;
    color: rgba(255, 255, 255, 0);
    background-size: 200% 100%;
    background-color: #0d168d;
    transition: 2s;
    cursor: default;
    background-clip: text;
    -webkit-background-clip: text;
    background-image: linear-gradient(-72deg,transparent 0,transparent 5%,rgba(184, 32, 32, 0.884),rgba(0,255,251,0.986),transparent 10%,transparent 100%);
  }

  .shine:hover{
    background-position: -140% 0;
  }